import uniStatusBar from '../uni-status-bar/uni-status-bar.vue'
import uniIcon from '../uni-icon/uni-icon.vue'

export default {
	name: 'UniNavBar',
	components: {
		uniStatusBar,
		uniIcon
	},
	props: {
		
		title: {
			type: String,
			default: ''
		},
		leftText: {
			type: String,
			default: ''
		},
		rightText: {
			type: String,
			default: ''
		},
		leftIcon: {
			type: String,
			default: ''
		},
		rightIcon: {
			type: String,
			default: ''
		},
		fixed: {
			type: Boolean,
			default: false
		},
		color: {
			type: String,
			default: 'white'
		},

		statusBar: {
			type: Boolean,
			default: false
		},
		shadow: {
			type: Boolean,
			default: true
		},
		border: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			statusHeight: 0,
			statusline: 0,
			backColor: 'background-image:linear-gradient( 135deg, #56ffea 10%, #f140a4 100%)',
			backStatus: 1
		}
	},
	mounted() {
		
		this.getTitleHeight()
	},
	methods: {
		onClickLeft() {
			this.$emit('click-left')
		},
		onClickRight() {
			this.$emit('click-right')
		},
		getTitleHeight() {
			let that = this;
			uni.getSystemInfo({
				success: res => {
					console.log(res.screenHeight)
					if (res.screenHeight < 740) {
						that.statusline = "90px"
						that.statusHeight = "60px"
						return false;
					}
					if (res.screenHeight < 950) {
						that.statusline = "100px"
						that.statusHeight = "70px"
						return false;
					}
					if (res.screenHeight > 1000) {
						that.statusline = "110px";
						that.statusHeight = "80px";
						return false;
					}
				}
			})
		}

	}
}
